<template>
  <div id="main-app">
    <v-toolbar app dark color="black" extended extension-height="2px" style="padding-left: 0px; z-index: 10;">
      <v-toolbar-side-icon @click="showNav = true">
        <v-icon dark color="pink accent-2">toys</v-icon>
      </v-toolbar-side-icon>
      <v-toolbar-title>
        Fuzz-Caster
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn
          flat
          href="https://www.instagram.com/immanuelherewele/?hl=en"
          target="_blank"
          icon
        >
          <font-awesome-icon class="white--text" :icon="['fab', 'instagram']" size="2x"></font-awesome-icon>
        </v-btn>
      </v-toolbar-items>
      <template v-slot:extension>
        <div class="indigo accent-4" style="width: 100%; height: 2px;"></div>
      </template>
    </v-toolbar>

    <v-navigation-drawer app clipped style="z-index: 11" width="220" v-model="showNav">
      <v-list>
        <template v-for="(menu, index) in menus">
          <v-list-tile
            :key="`side-nav-menu-${index}`"
            avatar
            :to="menu.to"
          >
            <v-list-tile-avatar>
              <v-avatar>
                <v-icon>{{menu.icon}}</v-icon>
              </v-avatar>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{ menu.text }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <!-- <v-divider :key="`side-nav-div-${index}`"/> -->
        </template>
      </v-list>
    </v-navigation-drawer>

    <!-- Application Loading Pane -->
    <v-container fluid fill-height v-if="loadingPaneShow" class="app-loader">
      <v-layout fill-height justify-center align-center>
        <v-progress-circular
          width="16"
          size="126"
          color="indigo"
          indeterminate
        >
        </v-progress-circular>
      </v-layout>
    </v-container>

    <!-- Snackbar for global message. -->
    <v-snackbar
      v-model="snackbarShow"
      :color="snackbarColor"
    >
      {{snackbarMessage}}
    </v-snackbar>

    <intercept-action/>

    <router-view/>

  </div>
</template>

<script>
import { menus } from "./App_menus";
import evbus from "@/evbus";
import InterceptAction from "../InterceptAction";

export default {
  components: {
    InterceptAction
  },
  data: () => ({
    loggedIn: false,
    menus,
    snackbarShow: false,
    showNav: true,
    snackbarColor: 'info',
    snackbarMessage: '',
    loadingPaneShow: false,
    interceptShow: false,
    interceptMessage: '',
    interceptAction: () => {}
  }),
  methods: {
    openAbout() {
      window.open("https://www.instagram.com/immanuelherewele/?hl=en", "_blank");
    }
  },
  mounted() {
    evbus.$on("success", msg => {
      this.snackbarShow = true;
      this.snackbarColor = "info";
      this.snackbarMessage = msg;
    });
    evbus.$on("error", msg => {
      this.snackbarShow = true;
      this.snackbarColor = "error";
      this.snackbarMessage = msg;
    });
    evbus.$on("loading-on", () => {
      this.loadingPaneShow = true;
    });
    evbus.$on("loading-off", () => {
      this.loadingPaneShow = false;
    });
    if (localStorage.getItem("nue2014.username") === null) {
      this.$router.push("/login");
    }
  },
  beforeDestroy() {
    evbus.$off("success");
    evbus.$off("error");
    evbus.$off("loading-on");
    evbus.$off("loading-off");
  }
}
</script>

<style lang="scss">
  #main-app {
    .v-toolbar__extension {
      padding: 0 !important;
    }

    .app-loader {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(255, 255, 255, 0.9);
      z-index: 999;
    }
  }
</style>
