<template>
  <v-dialog width="500" v-model="show">
    <v-container fluid fill-height class="grey lighten-3">
      <v-layout row fill-height>
        <v-flex md12 fill-height class="text-xs-center">
          <div class="title">{{message}}</div>
          <v-btn @click="runAction" large dark color="light-blue darken-2">Ya</v-btn>
          <v-btn large dark color="pink">Tidak?</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </v-dialog>
</template>

<script>
import evbus from "@/evbus";

export default {
  data: () => ({
    message: '',
    show: false,
    action: () => {}
  }),
  methods: {
    async runAction() {
      try {
        await this.action();
      } finally {
        this.show = false;
      }
    }
  },
  mounted() {
    evbus.$on("app-action", payload => {
      this.message = payload.message;
      this.action = payload.action;
      this.show = true;
    });
  },
  beforeDestroy() {
    evbus.$off("app-action");
  }
}
</script>

